"use client";

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Search, TrendingUp, TrendingDown, X, Plus, Minus, Maximize2, DollarSign, BarChart3 } from "lucide-react";
import { useState, useEffect } from "react";
import dynamic from 'next/dynamic';

// Dynamically import TradingChart to avoid SSR issues
const TradingChart = dynamic(() => import('@/components/TradingChart'), {
  ssr: false,
  loading: () => <div className="h-64 bg-muted animate-pulse rounded" />
});

interface StockQuote {
  symbol: string;
  name: string;
  price: number;
  change: number;
  changePercent: number;
  volume: number;
  marketCap: number;
  open?: number;
  high?: number;
  low?: number;
}

interface StockWindow {
  id: string;
  symbol: string;
  quote: StockQuote | null;
  quantity: number;
  orderType: "market" | "limit";
  limitPrice: number;
}

export default function TradePage() {
  const [stockWindows, setStockWindows] = useState<StockWindow[]>([
    { id: "1", symbol: "AAPL", quote: null, quantity: 1, orderType: "market", limitPrice: 0 },
  ]);
  const [searchQuery, setSearchQuery] = useState("");
  const [cashBalance, setCashBalance] = useState(0);
  const [portfolio, setPortfolio] = useState<any[]>([]);
  const [loading, setLoading] = useState<{ [key: string]: boolean }>({});

  useEffect(() => {
    fetchPortfolioData();
    // Load quotes for all windows
    stockWindows.forEach(window => {
      if (window.symbol) {
        fetchStockQuote(window.id, window.symbol);
      }
    });
  }, []);

  const fetchPortfolioData = async () => {
    try {
      const [balanceRes, holdingsRes] = await Promise.all([
        fetch("/api/portfolio/balance"),
        fetch("/api/portfolio/holdings"),
      ]);

      if (balanceRes.ok) {
        const balanceData = await balanceRes.json();
        setCashBalance(balanceData.cashBalance || 0);
      }

      if (holdingsRes.ok) {
        const holdingsData = await holdingsRes.json();
        setPortfolio(holdingsData);
      }
    } catch (error) {
      console.error("Error fetching portfolio:", error);
    }
  };

  const fetchStockQuote = async (windowId: string, symbol: string) => {
    if (!symbol.trim()) return;

    setLoading(prev => ({ ...prev, [windowId]: true }));
    try {
      const response = await fetch(`/api/stocks/quote/${symbol.toUpperCase()}`);
      if (response.ok) {
        const data = await response.json();
        const quote: StockQuote = {
          symbol: data.symbol || symbol.toUpperCase(),
          name: data.name || "Unknown",
          price: data.price || 0,
          change: data.change || 0,
          changePercent: data.changePercent || 0,
          volume: data.volume || 0,
          marketCap: data.marketCap || 0,
          open: data.open || data.price,
          high: data.high || data.price,
          low: data.low || data.price,
        };

        setStockWindows(prev => prev.map(w =>
          w.id === windowId
            ? { ...w, quote, limitPrice: quote.price, symbol: quote.symbol }
            : w
        ));
      } else {
        alert(`Stock ${symbol} not found`);
      }
    } catch (error) {
      console.error("Error fetching stock:", error);
    }
    setLoading(prev => ({ ...prev, [windowId]: false }));
  };

  const addStockWindow = () => {
    if (!searchQuery.trim()) return;

    const newWindow: StockWindow = {
      id: Date.now().toString(),
      symbol: searchQuery.toUpperCase(),
      quote: null,
      quantity: 1,
      orderType: "market",
      limitPrice: 0,
    };

    setStockWindows(prev => [...prev, newWindow]);
    fetchStockQuote(newWindow.id, searchQuery.toUpperCase());
    setSearchQuery("");
  };

  const removeStockWindow = (windowId: string) => {
    setStockWindows(prev => prev.filter(w => w.id !== windowId));
  };

  const updateWindow = (windowId: string, updates: Partial<StockWindow>) => {
    setStockWindows(prev => prev.map(w =>
      w.id === windowId ? { ...w, ...updates } : w
    ));
  };

  const executeTrade = async (windowId: string, action: "buy" | "sell") => {
    const window = stockWindows.find(w => w.id === windowId);
    if (!window || !window.quote || window.quantity <= 0) return;

    const price = window.orderType === "market" ? window.quote.price : window.limitPrice;
    const total = price * window.quantity;

    // Validation
    if (action === "buy" && total > cashBalance) {
      alert(`Insufficient funds. You have $${cashBalance.toFixed(2)} but need $${total.toFixed(2)}`);
      return;
    }

    if (action === "sell") {
      const holding = portfolio.find(h => h.symbol === window.quote!.symbol);
      if (!holding || holding.quantity < window.quantity) {
        alert(`Insufficient shares. You have ${holding?.quantity || 0} shares.`);
        return;
      }
    }

    setLoading(prev => ({ ...prev, [windowId]: true }));
    try {
      const response = await fetch("/api/portfolio/trade", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          symbol: window.quote.symbol,
          action,
          quantity: window.quantity,
          price,
          orderType: window.orderType,
        }),
      });

      if (response.ok) {
        alert(`âœ“ ${action.toUpperCase()} ${window.quantity} shares of ${window.quote.symbol} @ $${price.toFixed(2)}`);
        fetchPortfolioData();
        updateWindow(windowId, { quantity: 1 });
      } else {
        const error = await response.json();
        alert(`Order failed: ${error.message || "Unknown error"}`);
      }
    } catch (error) {
      console.error("Error executing order:", error);
      alert("Error executing order. Please try again.");
    }
    setLoading(prev => ({ ...prev, [windowId]: false }));
  };

  const refreshQuote = (windowId: string) => {
    const window = stockWindows.find(w => w.id === windowId);
    if (window && window.symbol) {
      fetchStockQuote(windowId, window.symbol);
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Multi-Window Trading</h1>
          <p className="text-muted-foreground">Monitor and trade multiple stocks simultaneously</p>
        </div>
        <div className="flex items-center gap-4">
          <Card className="border-green-500/20 bg-green-500/5">
            <CardContent className="p-4">
              <div className="flex items-center gap-2">
                <DollarSign className="h-5 w-5 text-green-500" />
                <div>
                  <p className="text-xs text-muted-foreground">Cash Balance</p>
                  <p className="text-lg font-bold text-green-500">
                    ${cashBalance.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Add Stock Window */}
      <Card className="border-blue-500/20 bg-blue-500/5">
        <CardContent className="p-4">
          <div className="flex gap-4">
            <Input
              placeholder="Enter stock symbol (e.g., AAPL, TSLA, NVDA)..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value.toUpperCase())}
              onKeyPress={(e) => e.key === "Enter" && addStockWindow()}
              className="flex-1 bg-background"
            />
            <Button onClick={addStockWindow} className="bg-blue-600 hover:bg-blue-700">
              <Plus className="mr-2 h-4 w-4" />
              Add Stock Window
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Stock Windows Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-4">
        {stockWindows.map((window) => (
          <Card
            key={window.id}
            className="border-2 hover:border-primary/50 transition-all duration-300 relative group"
          >
            {/* Window Header */}
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="flex-1">
                  {window.quote ? (
                    <>
                      <div className="flex items-center gap-2">
                        <CardTitle className="text-2xl">{window.quote.symbol}</CardTitle>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => refreshQuote(window.id)}
                          className="h-6 w-6 p-0"
                        >
                          <BarChart3 className="h-4 w-4" />
                        </Button>
                      </div>
                      <CardDescription className="text-xs truncate">
                        {window.quote.name}
                      </CardDescription>
                    </>
                  ) : (
                    <CardTitle className="text-xl">Loading {window.symbol}...</CardTitle>
                  )}
                </div>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => removeStockWindow(window.id)}
                  className="h-8 w-8 p-0 opacity-0 group-hover:opacity-100 transition-opacity"
                >
                  <X className="h-4 w-4" />
                </Button>
              </div>
            </CardHeader>

            <CardContent className="space-y-4">
              {window.quote && (
                <>
                  {/* Live Chart */}
                  <div className="mb-4">
                    <TradingChart symbol={window.symbol} height={250} showVolume={false} />
                  </div>

                  {/* Compact Price Display */}
                  <div className="bg-muted/30 p-3 rounded-lg">
                    <div className="flex items-baseline justify-between">
                      <span className="text-2xl font-bold">${window.quote.price.toFixed(2)}</span>
                      <div className={`flex items-center gap-1 text-xs font-semibold ${
                        window.quote.change >= 0 ? "text-green-500" : "text-red-500"
                      }`}>
                        {window.quote.change >= 0 ? <TrendingUp className="h-3 w-3" /> : <TrendingDown className="h-3 w-3" />}
                        {window.quote.change >= 0 ? "+" : ""}{window.quote.change.toFixed(2)}
                        ({window.quote.changePercent >= 0 ? "+" : ""}{window.quote.changePercent.toFixed(2)}%)
                      </div>
                    </div>
                  </div>

                  {/* Order Controls */}
                  <div className="space-y-3">
                    {/* Order Type */}
                    <div className="grid grid-cols-2 gap-2">
                      <Button
                        variant={window.orderType === "market" ? "default" : "outline"}
                        size="sm"
                        onClick={() => updateWindow(window.id, { orderType: "market" })}
                        className="text-xs"
                      >
                        Market
                      </Button>
                      <Button
                        variant={window.orderType === "limit" ? "default" : "outline"}
                        size="sm"
                        onClick={() => updateWindow(window.id, { orderType: "limit" })}
                        className="text-xs"
                      >
                        Limit
                      </Button>
                    </div>

                    {/* Limit Price */}
                    {window.orderType === "limit" && (
                      <div>
                        <label className="text-xs text-muted-foreground mb-1 block">Limit Price</label>
                        <Input
                          type="number"
                          step="0.01"
                          value={window.limitPrice}
                          onChange={(e) => updateWindow(window.id, { limitPrice: parseFloat(e.target.value) || 0 })}
                          className="h-8 text-sm"
                        />
                      </div>
                    )}

                    {/* Quantity */}
                    <div>
                      <label className="text-xs text-muted-foreground mb-1 block">Quantity</label>
                      <div className="flex items-center gap-2">
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => updateWindow(window.id, { quantity: Math.max(1, window.quantity - 1) })}
                          className="h-8 w-8 p-0"
                        >
                          <Minus className="h-3 w-3" />
                        </Button>
                        <Input
                          type="number"
                          min="1"
                          value={window.quantity}
                          onChange={(e) => updateWindow(window.id, { quantity: parseInt(e.target.value) || 1 })}
                          className="h-8 text-center text-sm"
                        />
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => updateWindow(window.id, { quantity: window.quantity + 1 })}
                          className="h-8 w-8 p-0"
                        >
                          <Plus className="h-3 w-3" />
                        </Button>
                      </div>
                    </div>

                    {/* Order Total */}
                    <div className="bg-muted/30 p-2 rounded text-sm">
                      <div className="flex justify-between">
                        <span className="text-muted-foreground">Total:</span>
                        <span className="font-bold">
                          ${((window.orderType === "market" ? window.quote.price : window.limitPrice) * window.quantity).toFixed(2)}
                        </span>
                      </div>
                    </div>

                    {/* Buy/Sell Buttons */}
                    <div className="grid grid-cols-2 gap-2">
                      <Button
                        onClick={() => executeTrade(window.id, "buy")}
                        disabled={loading[window.id]}
                        className="bg-green-600 hover:bg-green-700"
                      >
                        {loading[window.id] ? "..." : "Buy"}
                      </Button>
                      <Button
                        onClick={() => executeTrade(window.id, "sell")}
                        disabled={loading[window.id]}
                        variant="destructive"
                        className="bg-red-600 hover:bg-red-700"
                      >
                        {loading[window.id] ? "..." : "Sell"}
                      </Button>
                    </div>
                  </div>
                </>
              )}

              {!window.quote && loading[window.id] && (
                <div className="flex items-center justify-center h-48">
                  <div className="text-center">
                    <div className="animate-spin h-8 w-8 border-4 border-primary border-t-transparent rounded-full mx-auto mb-2"></div>
                    <p className="text-sm text-muted-foreground">Loading {window.symbol}...</p>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Empty State */}
      {stockWindows.length === 0 && (
        <Card className="border-dashed">
          <CardContent className="flex flex-col items-center justify-center py-16">
            <Search className="h-12 w-12 text-muted-foreground mb-4" />
            <h3 className="text-lg font-semibold mb-2">No stock windows</h3>
            <p className="text-sm text-muted-foreground mb-4">Add your first stock to start trading</p>
          </CardContent>
        </Card>
      )}

      {/* Portfolio Holdings Quick View */}
      {portfolio.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Your Holdings</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-2">
              {portfolio.map((holding: any) => (
                <Button
                  key={holding.symbol}
                  variant="outline"
                  size="sm"
                  onClick={() => {
                    const exists = stockWindows.find(w => w.symbol === holding.symbol);
                    if (!exists) {
                      const newWindow: StockWindow = {
                        id: Date.now().toString(),
                        symbol: holding.symbol,
                        quote: null,
                        quantity: 1,
                        orderType: "market",
                        limitPrice: 0,
                      };
                      setStockWindows(prev => [...prev, newWindow]);
                      fetchStockQuote(newWindow.id, holding.symbol);
                    }
                  }}
                  className="flex flex-col items-start h-auto p-2"
                >
                  <span className="font-bold text-xs">{holding.symbol}</span>
                  <span className="text-xs text-muted-foreground">{holding.quantity} shares</span>
                </Button>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
